import { Component, OnInit } from '@angular/core';
import { PopupService } from "../../../services/popup/popup.service";
import { ProductService } from 'src/app/idealz/services/product.service';
import { ToastrManager } from 'ng6-toastr-notifications';
import { CommonService } from 'src/app/idealz/services/common/common.service';
@Component({
  selector: 'app-inventory',
  templateUrl: './inventory.component.html',
  styleUrls: ['./inventory.component.scss']
})
export class InventoryComponent implements OnInit {
  inventoryList : any =[];

  constructor(private dialogService: PopupService,
    private productService:ProductService,
    private toastr:ToastrManager,
    private commonService:CommonService) {
      this.getAllProduct();
   }

  ngOnInit() {
    this.getAllProduct();
  }
getAllProduct(){
  this.productService.productListing().subscribe(res=>{
    this.inventoryList=res.data;
  }) 
}
  addProduct(){
    this.dialogService.addProduct().subscribe(res => {
      if(res){
          this.inventoryList.push(res);
          this.toastr.successToastr("Product added successfully", '', {
            maxShown: 1
          });
        }
    });
  }
  editProduct(id:any){
    localStorage.setItem("ProductId",id);
    this.dialogService.editProduct().subscribe(res => {
      if(res){
        this.getAllProduct();
        debugger
          this.toastr.successToastr("Product Edited Successfully", '', {
            maxShown: 1
          });
          window.location.reload()
    }
    });
  }
  deleteProduct(id:any){
 this.productService.deleteProduct(id).subscribe(res=>{
  this.getAllProduct();
  this.toastr.successToastr(res.uiMessage, '', {
    maxShown: 1
  });
 })
  }
}
